body {
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

  margin: 0;
	padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

	/* background-color: lightblue; */
	background: #2193b0; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#6dd5ed,
		#2193b0
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#6dd5ed,
		#2193b0
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.not-selected {
	user-select: none;
}


.App{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100% ;
}


header {
  color: #2193b0;
}


/*
===============
Markdown
===============
*/
.markdown,
.markdown-preview {
	min-height: 200px;
}

.markdown-preview {
	background-color: rgba(220, 220, 220, 0.6);
	/* background-color: rgba(220, 220, 220, 0.75); */
}

/* unique styles */
img {
	width: 200px;
	height: 200px;
	object-fit: cover;
}

blockquote {
  border-radius: .5rem;
	background: #eee;
  padding: .5rem ;
	font-size: 2rem;
	font-style: italic;
	border-left: 5px solid #555;
	padding-left: 2rem;
	line-height: 2;
	margin-bottom: 1.5rem;
}
blockquote ::first-letter {
	text-transform: uppercase;
}

pre {
	background: #555;
	display: block;
  border-radius: 5px;
	padding: .5rem 1rem;
	color: orange;
	margin-bottom: 1.5rem;
}



 	table {
 		border-collapse: collapse;
     width: 100%;
  	margin-bottom: 1rem;
    }

 	th {
 		font-weight: 600 !important;
 		border: 0px !important;
 		text-align: left;
 		background-color: #182034;
 		color: #eee;
 		font-weight: 700;
 		text-align: center;
 		font-size: 1.3rem;
 	}

 	td {
 		border-left: 0px !important;
 		border-right: 0px !important;
 	}

 	tr:nth-child(even) {
 		background-color: #409fff20;
 	}
 	tr:nth-child(odd) {
 		background-color: #409fff00;
 	}
 	tr:hover {
 		background-color: #1f7e9b55;
 	}



 	th,
 	td {
 		border: 1px solid #eee !important;
 		box-shadow: 0 0 2px #eee;
 	}


  hr {
 		border: none;
 		height: 0;
 		border-top: 1px solid #555;
 		border-radius:  5px;
     margin: 2rem 0;
 	}


   	input[type='checkbox'] {
		border: none;
		outline: none;
		transform: scale(1.5);
    margin-right: 10px;
	}

